const emptyBuffer = new ArrayBuffer(0)

/**
 * Error thrown when buffer validation fails (e.g., invalid UTF-8, missing terminator)
 */
export class BufferValidationError extends Error {
  constructor(message: string) {
    super(message)
    this.name = 'BufferValidationError'
  }
}

/**
 * Validate that a Uint8Array contains valid UTF-8.
 * Returns true if valid, false if invalid.
 */
function isValidUtf8(bytes: Uint8Array): boolean {
  let i = 0
  while (i < bytes.length) {
    const byte = bytes[i]
    if (byte === undefined) return false // Guard for noUncheckedIndexedAccess

    if (byte <= 0x7f) {
      // ASCII (0xxxxxxx)
      i++
    } else if ((byte & 0xe0) === 0xc0) {
      // 2-byte sequence (110xxxxx 10xxxxxx)
      if (byte < 0xc2) return false // Overlong encoding
      if (i + 1 >= bytes.length) return false
      const byte1 = bytes[i + 1]
      if (byte1 === undefined || (byte1 & 0xc0) !== 0x80) return false
      i += 2
    } else if ((byte & 0xf0) === 0xe0) {
      // 3-byte sequence (1110xxxx 10xxxxxx 10xxxxxx)
      if (i + 2 >= bytes.length) return false
      const byte1 = bytes[i + 1]
      const byte2 = bytes[i + 2]
      if (byte1 === undefined || byte2 === undefined) return false
      if ((byte1 & 0xc0) !== 0x80) return false
      if ((byte2 & 0xc0) !== 0x80) return false
      // Check for overlong encoding
      if (byte === 0xe0 && byte1 < 0xa0) return false
      // Check for surrogate pairs (U+D800-U+DFFF)
      if (byte === 0xed && byte1 >= 0xa0) return false
      i += 3
    } else if ((byte & 0xf8) === 0xf0) {
      // 4-byte sequence (11110xxx 10xxxxxx 10xxxxxx 10xxxxxx)
      if (byte > 0xf4) return false // Beyond Unicode range
      if (i + 3 >= bytes.length) return false
      const byte1 = bytes[i + 1]
      const byte2 = bytes[i + 2]
      const byte3 = bytes[i + 3]
      if (byte1 === undefined || byte2 === undefined || byte3 === undefined) return false
      if ((byte1 & 0xc0) !== 0x80) return false
      if ((byte2 & 0xc0) !== 0x80) return false
      if ((byte3 & 0xc0) !== 0x80) return false
      // Check for overlong encoding and beyond Unicode range
      if (byte === 0xf0 && byte1 < 0x90) return false
      if (byte === 0xf4 && byte1 > 0x8f) return false
      i += 4
    } else {
      // Invalid start byte
      return false
    }
  }
  return true
}

export class BufferReader {
  #bufferView: DataView = new DataView(emptyBuffer)
  #offset: number
  #messageEndOffset: number = 0 // End of current message (for bounds checking)

  // TODO(bmc): support non-utf8 encoding?
  readonly #encoding: string = 'utf-8' as const
  readonly #decoder = new TextDecoder(this.#encoding)
  readonly #littleEndian: boolean = false as const

  constructor(offset: number = 0) {
    this.#offset = offset
  }

  public setBuffer(
    offset: number,
    buffer: ArrayBuffer,
    messageLength?: number,
  ): void {
    this.#offset = offset
    this.#bufferView = new DataView(buffer)
    // messageLength includes the 4-byte length field, so message content ends at offset + (messageLength - 4)
    this.#messageEndOffset =
      messageLength !== undefined ? offset + (messageLength - 4) : buffer.byteLength
  }

  /**
   * Get remaining bytes in the current message
   */
  public remainingBytes(): number {
    return this.#messageEndOffset - this.#offset
  }

  public int16(): number {
    const result = this.#bufferView.getInt16(this.#offset, this.#littleEndian)
    this.#offset += 2
    return result
  }

  public byte(): number {
    const result = this.#bufferView.getUint8(this.#offset)
    this.#offset++
    return result
  }

  public int32(): number {
    const result = this.#bufferView.getInt32(this.#offset, this.#littleEndian)
    this.#offset += 4
    return result
  }

  public string(length: number, validateUtf8: boolean = false): string {
    const bytes = this.bytes(length)
    if (validateUtf8 && !isValidUtf8(bytes)) {
      throw new BufferValidationError('Invalid UTF-8 sequence')
    }
    const result = this.#decoder.decode(bytes)
    return result
  }

  /**
   * Read a string with validation that it doesn't exceed message bounds
   */
  public stringValidated(length: number): string {
    if (length < 0) {
      throw new BufferValidationError('Negative string length')
    }
    if (this.#offset + length > this.#messageEndOffset) {
      throw new BufferValidationError('String length exceeds message bounds')
    }
    return this.string(length, true)
  }

  public cstring(): string {
    const start = this.#offset
    let end = start
    while (this.#bufferView.getUint8(end++) !== 0) {
      // no-op - increment until terminator reached
    }
    const result = this.string(end - start - 1)
    this.#offset = end
    return result
  }

  /**
   * Read a null-terminated string with bounds checking
   * Throws BufferValidationError if no null terminator found within message bounds
   */
  public cstringValidated(): string {
    const start = this.#offset
    let end = start

    // Search for null terminator within message bounds
    while (end < this.#messageEndOffset) {
      if (this.#bufferView.getUint8(end) === 0) {
        // Found terminator
        const bytes = new Uint8Array(this.#bufferView.buffer, start, end - start)
        if (!isValidUtf8(bytes)) {
          throw new BufferValidationError('Invalid UTF-8 sequence in cstring')
        }
        const result = this.#decoder.decode(bytes)
        this.#offset = end + 1
        return result
      }
      end++
    }

    // No null terminator found within message bounds
    throw new BufferValidationError('Missing null terminator in cstring')
  }

  public bytes(length: number): Uint8Array {
    const result = this.#bufferView.buffer.slice(
      this.#offset,
      this.#offset + length,
    )
    this.#offset += length
    return new Uint8Array(result)
  }
}
