import {
  bindComplete,
  parseComplete,
  closeComplete,
  noData,
  portalSuspended,
  copyDone,
  replicationStart,
  emptyQuery,
  ReadyForQueryMessage,
  CommandCompleteMessage,
  CopyDataMessage,
  CopyResponse,
  NotificationResponseMessage,
  RowDescriptionMessage,
  ParameterDescriptionMessage,
  Field,
  DataRowMessage,
  ParameterStatusMessage,
  BackendKeyDataMessage,
  DatabaseError,
  NoticeMessage,
  AuthenticationOk,
  AuthenticationCleartextPassword,
  AuthenticationMD5Password,
  AuthenticationSASL,
  AuthenticationSASLContinue,
  AuthenticationSASLFinal,
} from './messages'
import type { BackendMessage, MessageName, AuthenticationMessage } from './messages'
import { Modes } from './types'
import type { BufferParameter } from './types'
import { BufferReader, BufferValidationError } from './buffer-reader'

// every message is prefixed with a single bye
const CODE_LENGTH = 1 as const
// every message has an int32 length which includes itself but does
// NOT include the code in the length
const LEN_LENGTH = 4 as const

const HEADER_LENGTH = CODE_LENGTH + LEN_LENGTH

// Maximum reasonable field count to prevent DoS attacks
// PostgreSQL protocol uses int16 for field counts, so max is 32767
const MAX_FIELD_COUNT = 1664 // PostgreSQL's default max_columns

export type Packet = {
  code: number
  packet: ArrayBuffer
}

const emptyBuffer = new ArrayBuffer(0)

const enum MessageCodes {
  DataRow = 0x44, // D
  ParseComplete = 0x31, // 1
  BindComplete = 0x32, // 2
  CloseComplete = 0x33, // 3
  CommandComplete = 0x43, // C
  ReadyForQuery = 0x5a, // Z
  NoData = 0x6e, // n
  NotificationResponse = 0x41, // A
  AuthenticationResponse = 0x52, // R
  ParameterStatus = 0x53, // S
  BackendKeyData = 0x4b, // K
  ErrorMessage = 0x45, // E
  NoticeMessage = 0x4e, // N
  RowDescriptionMessage = 0x54, // T
  ParameterDescriptionMessage = 0x74, // t
  PortalSuspended = 0x73, // s
  ReplicationStart = 0x57, // W
  EmptyQuery = 0x49, // I
  CopyIn = 0x47, // G
  CopyOut = 0x48, // H
  CopyDone = 0x63, // c
  CopyData = 0x64, // d
}

export type MessageCallback = (msg: BackendMessage) => void

export class Parser {
  #bufferView: DataView = new DataView(emptyBuffer)
  #bufferRemainingLength: number = 0
  #bufferOffset: number = 0
  #reader = new BufferReader()

  public parse(buffer: BufferParameter, callback: MessageCallback) {
    this.#mergeBuffer(
      ArrayBuffer.isView(buffer)
        ? (buffer.buffer.slice(
            buffer.byteOffset,
            buffer.byteOffset + buffer.byteLength,
          ) as ArrayBuffer)
        : buffer,
    )
    const bufferFullLength = this.#bufferOffset + this.#bufferRemainingLength
    let offset = this.#bufferOffset
    while (offset + HEADER_LENGTH <= bufferFullLength) {
      // code is 1 byte long - it identifies the message type
      const code = this.#bufferView.getUint8(offset)
      // length is 1 Int32BE - it is the length of the message EXCLUDING the code
      // Read as signed int32 to detect negative lengths
      const length = this.#bufferView.getInt32(offset + CODE_LENGTH, false)

      // Validate length field
      if (length < 4) {
        // Length must be at least 4 (the length field itself)
        // This catches both negative lengths and lengths < 4
        callback(
          new DatabaseError(
            `Invalid message length: ${length} (minimum is 4)`,
            length,
            'error',
          ),
        )
        offset += HEADER_LENGTH // Skip the invalid header
        continue
      }

      const fullMessageLength = CODE_LENGTH + length
      if (fullMessageLength + offset <= bufferFullLength) {
        const message = this.#handlePacket(
          offset + HEADER_LENGTH,
          code,
          length,
          this.#bufferView.buffer as ArrayBuffer,
        )
        callback(message)
        offset += fullMessageLength
      } else {
        break
      }
    }
    if (offset === bufferFullLength) {
      // No more use for the buffer
      this.#bufferView = new DataView(emptyBuffer)
      this.#bufferRemainingLength = 0
      this.#bufferOffset = 0
    } else {
      // Adjust the cursors of remainingBuffer
      this.#bufferRemainingLength = bufferFullLength - offset
      this.#bufferOffset = offset
    }
  }

  #mergeBuffer(buffer: ArrayBuffer): void {
    if (this.#bufferRemainingLength > 0) {
      const newLength = this.#bufferRemainingLength + buffer.byteLength
      const newFullLength = newLength + this.#bufferOffset
      if (newFullLength > this.#bufferView.byteLength) {
        // We can't concat the new buffer with the remaining one
        let newBuffer: ArrayBuffer
        if (
          newLength <= this.#bufferView.byteLength &&
          this.#bufferOffset >= this.#bufferRemainingLength
        ) {
          // We can move the relevant part to the beginning of the buffer instead of allocating a new buffer
          newBuffer = this.#bufferView.buffer as ArrayBuffer
        } else {
          // Allocate a new larger buffer
          let newBufferLength = this.#bufferView.byteLength * 2
          while (newLength >= newBufferLength) {
            newBufferLength *= 2
          }
          newBuffer = new ArrayBuffer(newBufferLength)
        }
        // Move the remaining buffer to the new one
        new Uint8Array(newBuffer).set(
          new Uint8Array(
            this.#bufferView.buffer,
            this.#bufferOffset,
            this.#bufferRemainingLength,
          ),
        )
        this.#bufferView = new DataView(newBuffer)
        this.#bufferOffset = 0
      }

      // Concat the new buffer with the remaining one
      new Uint8Array(this.#bufferView.buffer).set(
        new Uint8Array(buffer),
        this.#bufferOffset + this.#bufferRemainingLength,
      )
      this.#bufferRemainingLength = newLength
    } else {
      this.#bufferView = new DataView(buffer)
      this.#bufferOffset = 0
      this.#bufferRemainingLength = buffer.byteLength
    }
  }

  #handlePacket(
    offset: number,
    code: number,
    length: number,
    bytes: ArrayBuffer,
  ): BackendMessage {
    try {
      switch (code) {
        case MessageCodes.BindComplete:
          return bindComplete
        case MessageCodes.ParseComplete:
          return parseComplete
        case MessageCodes.CloseComplete:
          return closeComplete
        case MessageCodes.NoData:
          return noData
        case MessageCodes.PortalSuspended:
          return portalSuspended
        case MessageCodes.CopyDone:
          return copyDone
        case MessageCodes.ReplicationStart:
          return replicationStart
        case MessageCodes.EmptyQuery:
          return emptyQuery
        case MessageCodes.DataRow:
          return this.#parseDataRowMessage(offset, length, bytes)
        case MessageCodes.CommandComplete:
          return this.#parseCommandCompleteMessage(offset, length, bytes)
        case MessageCodes.ReadyForQuery:
          return this.#parseReadyForQueryMessage(offset, length, bytes)
        case MessageCodes.NotificationResponse:
          return this.#parseNotificationMessage(offset, length, bytes)
        case MessageCodes.AuthenticationResponse:
          return this.#parseAuthenticationResponse(offset, length, bytes)
        case MessageCodes.ParameterStatus:
          return this.#parseParameterStatusMessage(offset, length, bytes)
        case MessageCodes.BackendKeyData:
          return this.#parseBackendKeyData(offset, length, bytes)
        case MessageCodes.ErrorMessage:
          return this.#parseErrorMessage(offset, length, bytes, 'error')
        case MessageCodes.NoticeMessage:
          return this.#parseErrorMessage(offset, length, bytes, 'notice')
        case MessageCodes.RowDescriptionMessage:
          return this.#parseRowDescriptionMessage(offset, length, bytes)
        case MessageCodes.ParameterDescriptionMessage:
          return this.#parseParameterDescriptionMessage(offset, length, bytes)
        case MessageCodes.CopyIn:
          return this.#parseCopyInMessage(offset, length, bytes)
        case MessageCodes.CopyOut:
          return this.#parseCopyOutMessage(offset, length, bytes)
        case MessageCodes.CopyData:
          return this.#parseCopyData(offset, length, bytes)
        default:
          return new DatabaseError(
            'received invalid response: ' + code.toString(16),
            length,
            'error',
          )
      }
    } catch (err) {
      if (err instanceof BufferValidationError) {
        return new DatabaseError(`Malformed message: ${err.message}`, length, 'error')
      }
      throw err
    }
  }

  #parseReadyForQueryMessage(offset: number, length: number, bytes: ArrayBuffer) {
    this.#reader.setBuffer(offset, bytes, length)
    const status = this.#reader.string(1)
    return new ReadyForQueryMessage(length, status)
  }

  #parseCommandCompleteMessage(offset: number, length: number, bytes: ArrayBuffer) {
    this.#reader.setBuffer(offset, bytes, length)
    const text = this.#reader.cstringValidated()
    return new CommandCompleteMessage(length, text)
  }

  #parseCopyData(offset: number, length: number, bytes: ArrayBuffer) {
    const chunk = bytes.slice(offset, offset + (length - 4))
    return new CopyDataMessage(length, new Uint8Array(chunk))
  }

  #parseCopyInMessage(offset: number, length: number, bytes: ArrayBuffer) {
    return this.#parseCopyMessage(offset, length, bytes, 'copyInResponse')
  }

  #parseCopyOutMessage(offset: number, length: number, bytes: ArrayBuffer) {
    return this.#parseCopyMessage(offset, length, bytes, 'copyOutResponse')
  }

  #parseCopyMessage(
    offset: number,
    length: number,
    bytes: ArrayBuffer,
    messageName: MessageName,
  ) {
    this.#reader.setBuffer(offset, bytes, length)
    const isBinary = this.#reader.byte() !== 0
    const columnCount = this.#reader.int16()

    // Validate column count against available data
    // Each column type requires 2 bytes (int16)
    // Available bytes = length - 4 (length field) - 1 (binary flag) - 2 (column count)
    const availableBytes = length - 4 - 1 - 2
    const requiredBytes = columnCount * 2
    if (columnCount < 0 || requiredBytes > availableBytes) {
      throw new BufferValidationError(
        `Invalid column count ${columnCount}: requires ${requiredBytes} bytes but only ${availableBytes} available`,
      )
    }

    const message = new CopyResponse(length, messageName, isBinary, columnCount)
    for (let i = 0; i < columnCount; i++) {
      message.columnTypes[i] = this.#reader.int16()
    }
    return message
  }

  #parseNotificationMessage(offset: number, length: number, bytes: ArrayBuffer) {
    this.#reader.setBuffer(offset, bytes, length)
    const processId = this.#reader.int32()
    const channel = this.#reader.cstringValidated()
    const payload = this.#reader.cstringValidated()
    return new NotificationResponseMessage(length, processId, channel, payload)
  }

  #parseRowDescriptionMessage(offset: number, length: number, bytes: ArrayBuffer) {
    this.#reader.setBuffer(offset, bytes, length)
    const fieldCount = this.#reader.int16()

    // Validate field count
    if (fieldCount < 0 || fieldCount > MAX_FIELD_COUNT) {
      throw new BufferValidationError(
        `Invalid field count: ${fieldCount} (must be 0-${MAX_FIELD_COUNT})`,
      )
    }

    // Each field requires at least 19 bytes (name null terminator + 18 bytes of fixed data)
    // Check if declared field count is plausible given message length
    const minBytesPerField = 19
    const availableBytes = length - 4 - 2 // Subtract length field and field count
    if (fieldCount > 0 && fieldCount * minBytesPerField > availableBytes) {
      throw new BufferValidationError(
        `Field count ${fieldCount} exceeds available message data`,
      )
    }

    const message = new RowDescriptionMessage(length, fieldCount)
    for (let i = 0; i < fieldCount; i++) {
      message.fields[i] = this.#parseField()
    }
    return message
  }

  #parseField(): Field {
    const name = this.#reader.cstringValidated()
    const tableID = this.#reader.int32()
    const columnID = this.#reader.int16()
    const dataTypeID = this.#reader.int32()
    const dataTypeSize = this.#reader.int16()
    const dataTypeModifier = this.#reader.int32()
    const mode = this.#reader.int16() === 0 ? Modes.text : Modes.binary
    return new Field(name, tableID, columnID, dataTypeID, dataTypeSize, dataTypeModifier, mode)
  }

  #parseParameterDescriptionMessage(offset: number, length: number, bytes: ArrayBuffer) {
    this.#reader.setBuffer(offset, bytes, length)
    const parameterCount = this.#reader.int16()
    const message = new ParameterDescriptionMessage(length, parameterCount)
    for (let i = 0; i < parameterCount; i++) {
      message.dataTypeIDs[i] = this.#reader.int32()
    }
    return message
  }

  #parseDataRowMessage(offset: number, length: number, bytes: ArrayBuffer) {
    this.#reader.setBuffer(offset, bytes, length)
    const fieldCount = this.#reader.int16()

    // Validate field count
    if (fieldCount < 0 || fieldCount > MAX_FIELD_COUNT) {
      throw new BufferValidationError(
        `Invalid field count: ${fieldCount} (must be 0-${MAX_FIELD_COUNT})`,
      )
    }

    const fields: (string | null)[] = new Array(fieldCount)
    for (let i = 0; i < fieldCount; i++) {
      const len = this.#reader.int32()
      // a -1 for length means the value of the field is null
      if (len === -1) {
        fields[i] = null
      } else if (len < 0) {
        throw new BufferValidationError(`Invalid field length: ${len}`)
      } else {
        fields[i] = this.#reader.stringValidated(len)
      }
    }
    return new DataRowMessage(length, fields)
  }

  #parseParameterStatusMessage(offset: number, length: number, bytes: ArrayBuffer) {
    this.#reader.setBuffer(offset, bytes, length)
    const name = this.#reader.cstringValidated()
    const value = this.#reader.cstringValidated()
    return new ParameterStatusMessage(length, name, value)
  }

  #parseBackendKeyData(offset: number, length: number, bytes: ArrayBuffer) {
    this.#reader.setBuffer(offset, bytes, length)
    const processID = this.#reader.int32()
    const secretKey = this.#reader.int32()
    return new BackendKeyDataMessage(length, processID, secretKey)
  }

  #parseAuthenticationResponse(
    offset: number,
    length: number,
    bytes: ArrayBuffer,
  ): AuthenticationMessage {
    this.#reader.setBuffer(offset, bytes, length)
    const code = this.#reader.int32()
    switch (code) {
      case 0:
        return new AuthenticationOk(length)
      case 3:
        return new AuthenticationCleartextPassword(length)

      case 5:
        return new AuthenticationMD5Password(length, this.#reader.bytes(4))

      case 10: {
        const mechanisms: string[] = []
        while (true) {
          const mechanism = this.#reader.cstring()
          if (mechanism.length === 0) {
            return new AuthenticationSASL(length, mechanisms)
          }
          mechanisms.push(mechanism)
        }
      }
      case 11:
        return new AuthenticationSASLContinue(length, this.#reader.string(length - 8))

      case 12:
        return new AuthenticationSASLFinal(length, this.#reader.string(length - 8))

      default:
        throw new Error('Unknown authenticationOk message type ' + code)
    }
  }

  #parseErrorMessage(
    offset: number,
    length: number,
    bytes: ArrayBuffer,
    name: MessageName,
  ) {
    this.#reader.setBuffer(offset, bytes, length)
    const fields: Record<string, string> = {}
    let fieldType = this.#reader.string(1)
    while (fieldType !== '\0') {
      // Check remaining bytes before reading cstring
      if (this.#reader.remainingBytes() < 1) {
        throw new BufferValidationError('Malformed error message: missing field terminator')
      }
      fields[fieldType] = this.#reader.cstringValidated()
      if (this.#reader.remainingBytes() < 1) {
        throw new BufferValidationError('Malformed error message: missing final terminator')
      }
      fieldType = this.#reader.string(1)
    }

    const messageValue = fields.M ?? ''

    const message =
      name === 'notice'
        ? new NoticeMessage(length, messageValue)
        : new DatabaseError(messageValue, length, name)

    message.severity = fields.S
    message.code = fields.C
    message.detail = fields.D
    message.hint = fields.H
    message.position = fields.P
    message.internalPosition = fields.p
    message.internalQuery = fields.q
    message.where = fields.W
    message.schema = fields.s
    message.table = fields.t
    message.column = fields.c
    message.dataType = fields.d
    message.constraint = fields.n
    message.file = fields.F
    message.line = fields.L
    message.routine = fields.R
    return message
  }
}
